@import '../../styles/vars.scss';
.tags-page {
  .tags-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    grid-auto-rows: min-content;
    .item {
      border-radius: 5px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
      min-width: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      color: white;
      cursor: pointer;
      font-size: 1.5rem;
      transition: 0.3s;

      &:hover {
        transform: scale(1.03);
        opacity: 0.5;
        position: relative;
        &:not(.add)::after {
          position: absolute;
          width: 30px;
          height: 30px;
          content: "\f044";
          font-family: FontAwesome;
          font-size: 2rem;
          line-height: 1;
          color: black;

        } 
      }
      font-weight: bold;
      height: 100px;
      &.add {
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0.5;
        color: #aaa;
        cursor: pointer;
        box-shadow: none;
        transition: 0.3s;
        font-size: 2rem;
        &:hover {
          opacity: 1;
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
          color: $main-dark;
        }
      }
    }
    
  }
}