  border-radius: 5px;
  background-color: #fff;
  color: $main-dark;
  display: flex;
  align-items: center;
  justify-content: space-between;
  input {
    border: none;
    width: 400px;
    font-family: $main-font;
    font-size: 0.8rem;
    outline: none;
  }
}