@import '../../styles/vars.scss';

.auth-component-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.formblock {
  padding: 50px;
  width: 390px;
  background: #FDFDFD;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  > .title {
    text-align: center;
    font-size: 1.8rem;
    font-weight: 600;
    color: $main-blue;
    margin-bottom: 10px;
  }
  > .des {
    text-align: center;
    color: $main-gray;
    font-size: 1rem;
  }
  .errors {
    background-color: $lightpink;
    padding: 10px;
    margin-bottom: 50px;
    .item {
      font-size: 0.8rem;
      color: white;
    }
  }
  .inputs {
    margin-top: 80px;
    margin-bottom: 50px;
    > .item {
      display: flex;
      border-bottom: 1px solid $main-gray;
      padding-bottom: 10px;
      margin-top: 20px;
      position: relative;
      .icon {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .select {
        margin-left: 10px;
        width: 230px;
        flex-shrink: 0;
      }
      i {
        font-size: 1.5rem;
        color: $main-gray;
        width: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      textarea {
        // height: contain !important;
      }
      input, textarea {
        //border: 1px solid $main-gray;
        border: none;
        font-size: 0.8rem;
        outline: none;
        position: relative;
        margin-left: 10px;
        color: $main-dark;
        font-weight: 300;
        padding: 6px 10px;
        width: 100%;
        font-family: $main-font;
        &:focus {
          font-weight: 700;
        }
        &.wrong {
          border-bottom: 1px solid $lightpink
        }
      }
    }
  }

  button {
    display: flex;
    width: 100%;
    font-size: 1.2rem;
    color: white;
    font-weight: 800;
    
    border: none;
    cursor: pointer;
    font-family: $main-font;
    align-items: center;
    justify-content: center;
    height: 57px;
    transition: 0.3s;
    border-radius: 5px;
    background-color: $main-blue;
    &:hover {
      filter: brightness(1.1);
    }
  }

  .donthave {
    margin-top: 20px;
    font-size: 0.9rem;
    color: $main-gray;
    text-align: center;
    a {
      color: $main-blue;
      font-weight: 800;
    }
  }

  &.add-tag {
    width: auto;
    box-shadow: none;
    padding: 0;
    background-color: #fff;
    
      > .title {
        //color: $main-dark;
      }
    
  }
}