@import '../../../styles/vars.scss';
.user-info {
  display: flex;
  position: relative;
  align-items: center;
  .img {
    width: 35px;
    height: 35px;
    border-radius: 5px;
    background-color: #fff;
    margin-right: 15px;
    background-size: cover;
  }
  .username {
    margin-right: 15px;
    font-size: 0.9rem;
  }
  .icon {
    cursor: pointer;
    font-family: FontAwesome;
  }
  .hidden-menu {
    position: absolute;
    z-index: 11;
    background-color: #fff;
    top: 40px;
    width: 170px;
    user-select: none;
    right: 0;
    border-radius: 5px;
    padding-top: 15px;
    padding-bottom: 15px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    a {
      &:first-of-type {
        cursor: not-allowed;
        opacity: 0.5;
        &:hover {
          text-decoration: none;
        }
      }
      display: block;
      text-decoration: none;
      color: $main-dark;
      font-weight: 400;
      padding: 0px 15px;
      margin-bottom: 15px;
      font-size: 0.8rem;
      &:hover {
        text-decoration: underline;
      }
    }
    .line {
      margin-bottom: 15px;
      border-bottom: 1px solid rgb(223, 223, 223);
    }
    .logout {
      font-weight: 700;
      color: $main-dark;
      padding: 0px 15px;
      display: flex;
      cursor: pointer;
      align-items: center;
      &:hover span {
        text-decoration: underline;
      }
      span {
        margin-right: 10px;
        font-size: 0.8rem;
      }
    }
  }
}