.header-nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  height: 100%;
  align-items: stretch;
  a {
    display: flex;
    color: inherit;
    text-decoration: none;
    align-items: center;
    justify-content: center;
    font-size: 0.9rem;
    line-height: 1;
    box-sizing: border-box;
    font-weight: 700;
    cursor: pointer;
    margin-left: 15px;
    margin-right: 15px;
    height: 100%;
    border: 4px solid transparent;
    
    &.selected {
      border-top: 4px solid white;
    }
  }
}