@import '../../styles/vars.scss';
.tags-component {
  display: flex;
  min-height: 50px;
  justify-content: flex-start;
  flex-wrap: wrap;
  //border-top: 3px solid rgb(236, 92, 25);
  //border-top: 2px solid rgb(255, 255, 255);
  position: relative;
  .add-tag-form {
    position: absolute;
    background-color: #fff;
    padding: 15px;
    right: 20px;
    top: -20px;
    z-index: 10;
    border-radius: 5px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    .icons {
      display: flex;
    }
    .icon {
      display: flex;
      align-items: center;
      height: 35px;
      color: white;
      cursor: pointer;
      flex: 1;
      transition: 0.3s;
      &:hover {
        opacity: 0.8;
      }
      &.exit {
        color: $red;
      }
      //width: 50%;
      //margin-left: auto;
      justify-content: center;
      background-color: #efefef;
      border: 1px solid white;
      color: $green;
    }
    input {
      font-family: $main-font;
      padding: 6px 15px;
      
      height: 35px;
      border: 1px solid #efefef;
      background-color: #fff;
      outline: none;
      display: block;
      margin-bottom: 10px;
      font-size: 0.8rem;
      
    }
  }
  .tag-item {
    &.selected {
      position: relative;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      overflow: hidden;
      &::after {
        width: 10px;
        height: 10px;
        background-color: #fff;
        transform: rotate(45deg);
        position: absolute;
        left: calc(50% - 5px);
        content: '';
        top: -5px;
        
      }
    }

    display: flex;
    margin: 5px 0 0 5px;
    //border: 2px solid white;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    //box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding-left: 20px;
    padding-right: 20px;
    font-weight: 700;
    text-transform: uppercase;
    color: white;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      opacity: 0.7;
    }
    &.all {
      color: $main-dark;
      background-color: #efefef;
    }
    &.add {
      color: #888;
      padding-left: 15px;
      padding-right: 15px;

    }
  }
}