@import '../../styles/vars.scss';
.chunks-component {
  .chunks-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    grid-auto-rows: min-content;
    .chunk-item {
      border-radius: 5px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
      min-width: 100px;
      height: 300px;
      //margin: 20px;
      &.new-chunk {
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0.5;
        color: #aaa;
        cursor: pointer;
        box-shadow: none;
        transition: 0.3s;
        font-size: 2rem;
        &:hover {
          opacity: 1;
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
          color: $main-dark;
        }
      }
      .heading {
        color: $main-dark;
        padding: 20px;
        height: 136px;
        border-bottom: 1px solid $main-dark;
        background-color: #efefef;
        .title {
          font-size: 1.2rem;
          display: block;
          color: inherit;
          text-decoration: none;

          font-weight: 700;
          margin-bottom: 5px;
          cursor: pointer;
          &:hover {
            text-decoration: underline;
          }
        }
        .des {
          font-size: 0.8rem;
          font-weight: 400;
          margin-bottom: 15px;
          max-height: 30px;
          overflow: hidden;
        }
        .tags {
          
          align-items: center;
          display: flex;
          overflow-y: visible;
  
          max-width: 100%;
        
          overflow-x: scroll; 
          font-size: 0.7rem;
          .item {
            flex-shrink: 0;
            display: inline-block;
            margin-right: 8px;
            padding: 4px 8px;
            cursor: pointer;
            border-radius: 5px;
            color: white;
            transition: 0.3s;
            &:hover {
              opacity: 0.7;
            }
          }
        }
        position: relative;
        .language {
          font-size: 0.7rem;
          padding: 5px 10px;
          position: absolute;
          bottom: 0;
          background-color: #ccc;
          right: 0;
        }
      }
      .code {
        font-size: 0.8rem;
        background-color: #fff;
        padding: 15px;
        height: 164px;
        pre {
          margin: 0;
          max-height: 100%;
          background-color: #fff !important;
          
        }
      }
    }
  }
}