@import '../../styles/vars.scss';
.modal-wrapper {
  display: flex;
  align-items: center;
  height: 100vh;
  position: fixed;
  top: 0;
  width: 100%;
  // test
  justify-content: center;
  .modal-overlay {
    width: 100%;
    height: 100vh;
    background: rgba(1,1,1,0.3);
    position: fixed;
    z-index: 999;
    top: 0;
  }
  .modal-content {
    .formblock.shure {
      padding: 0;
      width: auto;
      box-shadow: none;
      .button-component {
        margin: 10px;
      }
      .title {
        margin-top: 23px;
      }
    }
    width: 450px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    position: relative;
    //margin-top: -250px;
    z-index: 1000;
    border-radius: 5px;
    padding: 30px 40px;
    max-height: 90vh;
    overflow: scroll;
    background-color: #fff;
    text-align: center;
    .close-button {
      position: absolute;
      right: 5px;
      color: $main-blue;
      cursor: pointer;
      font-size: 15px;
      padding: 10px;
      top: 2px;
    }
    .sketch-picker {
      width: 90% !important;
      margin: 0 auto;
      margin-top: 20px;
    }
  }
}


