@import '../../styles/vars.scss';
.button-component {
  font-size: 1rem;
  padding: 0 30px;
  line-height: 1;
  height: 45px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 5px;
  .cssload-container {
    flex-shrink: 0;
    height: 1rem;
    .cssload-zenith {
      height: 1rem;
      width: 1rem;
    }
  }
  cursor: pointer;
  transition: 0.3s;
  &:hover {
    opacity: 0.7;
  }
  &.red {
    color: white;
    background-color: $red;
  }
  &.green {
    color: white;
    background-color: $green;
  }
  &.black {
    color: white;
    background-color: #333;
  }
}