@import '../../styles/vars.scss';

.chunks-page-component {
  color: $main-dark;
  padding: 20px;
  
  > .title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 10px;
    position: relative;
    margin-top: 10px;
    
    //text-transform: uppercase;
  }
  
  > .des {
    font-weight: 1rem;
    line-height: 1.4;
    margin-bottom: 20px;
  }
  > .input {
    display: block;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid hsl(0,0%,80%);
    outline: none;
    &.wrong {
      border: 1px solid $red;
    }

  }
  > .tags {
    display: flex;
    margin-bottom: 23px;
    &.wrong {
      border: 1px solid $red;
    }
    &.editable {
      margin-bottom: 20px;
    }
    .tag-item {
      font-size: 0.9rem;
      line-height: 1;
      padding: 5px 10px;
      border-radius: 5px;
      color: white;
      font-weight: bold;
      margin-right: 10px;
    }
  }
  > .code {
    font-size: 0.9rem;
    padding: 10px;
    
    position: relative;
    background-color: #efefef;
    pre {
      margin: 0;
      background-color: #fff !important;
    }
    > .lang {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;
      height: 30px;
      font-size: 0.8rem;
      background-color: #efefef;
      display: inline-flex;
      align-items: center;
      padding: 0 20px;
      justify-content: center;
      margin-bottom: 20px;
    }
  }
  > .button-component {
    margin-top: 20px;
    &.black {
      margin-left: 10px;
    }
  }
  .ace-editor {
    z-index: 0;
    &.wrong {
      border: 1px solid $red;
    }
    border-radius: 5px;
  }
  > .apply-buttons {
    margin-top: 20px;
    display: flex;
    align-items: center;
    > div {
      margin-right: 10px;
    }
  }
}